import React, { Component } from "react";
import "./TopAlbumsDecade.css"
import Typed from 'react-typed';
import {TOP_ALBUMS_DECADE as TOP_DECADE_ALBUMS} from "./Constants"


class TopAlbums extends Component {
  constructor(props) {
    super(props);
    this.styles = this.props.styles
    this.state = { isOpen: false, album_id: null }
  }
  render() {

  const contentStyle = {
    paddingTop: this.styles.topBarHeight + 20,
    paddingRight: 20,
    paddingBottom: this.styles.footerMenuHeight + 20,
    paddingLeft: 20,
    backgroundColor: this.styles.black(),
    color: 'white',
  };

  const year = this.year
  const albums = TOP_DECADE_ALBUMS
  const top_albums = albums.slice(0,7)
  const mid_albums = albums.slice(8,15)


    return (
      <div style={contentStyle} >
        <div className={'content_container_decade'}>
          <div className='page_title_decade'><h1>
          <Typed
                    strings={['Best Albums Of The Decade']}
                    typeSpeed={50}
                    showCursor={false}
                />
            </h1></div>
          {albums.map((album_entry, i) => {
            return (
              <div className={'album_image ' + (i<10 ? 'album_top' : i<20 ? 'album_mid' : 'album_low')} key={i} style={{ marginBottom: 40}}>
                <a target={"_blank"} href={album_entry.spotify_link}><h2 className={'album-title'} style={{ marginBottom: 0 }}>{album_entry.artist + ' - ' + album_entry.album}</h2></a>
                  <img src={album_entry.cover_link} alt={album_entry.album} />
              </div>
            );
          })}
        </div>
      </div>
    );
  };
}

export default TopAlbums;